html {
  font-size: 62.5%;
  box-sizing: border-box;
}
.calculator {
  border: 1px solid #ccc;
  border-radius: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  
  width: 400px;
  }
  button {
    height: 67.5px;
    font-size: 2rem!important;
    }
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

.card {
box-shadow: 0 2px 5px 0 rgba(0,0,0,.16), 0 2px 10px 0 rgba(0,0,0,.12);
border: 0;
font-weight: 400;
}
.calculator-screen {
width: 100%;
height: 55px;
border: none;
background-color: #f2f2f2;
color: #59caec;
text-align: right;
padding-right: 20px;
padding-left: 20px;
font-size: 4rem;
}
.equal-sign {
grid-area:5 / 3 / 6 / 5
}
.calculator-keys {
display: grid;
grid-template-columns: repeat(4, 1fr);
grid-gap: 20px;
padding: 20px;
background: #f2f2f2;
}
